import React from 'react';

import { useParams } from 'react-router-dom';

import { useCategory } from '../../../lib/hooks';
import ListView from '../common/ListView';
import RouteViewer from './../../components/kural/RouteViewer';

type ChapterViewerProps = {
  className?: string;
};

const ChapterViewer: React.FC<ChapterViewerProps> = (
  props: ChapterViewerProps
) => {
  const { literature } = useParams<{ literature: string }>();
  const [items, onSelected] = useCategory('literature', Number(literature));
  return (
    <>
      <RouteViewer />
      <ListView
        className={props.className}
        items={items}
        onItemSelected={(item): void => onSelected(item)}
      />
    </>
  );
};

export default ChapterViewer;
