import React from 'react';

import { useParams } from 'react-router-dom';
import { useCategory } from 'src/lib/hooks';

import ListView from '../common/ListView';
import RouteViewer from './../../components/kural/RouteViewer';

type LiteratureViewerProps = {
  className?: string;
};

const LiteratureViewer: React.FC<LiteratureViewerProps> = (
  props: LiteratureViewerProps
) => {
  const { section } = useParams();
  const [items, onSelected] = useCategory('section', section);
  return (
    <>
      <RouteViewer />
      <ListView
        className={props.className}
        items={items}
        onItemSelected={(item): void => onSelected(item)}
      />
    </>
  );
};

export default LiteratureViewer;
