/* eslint-disable @typescript-eslint/no-non-null-assertion */
import React from 'react';

import { useSelector } from 'react-redux';
import {
  CategoryBaseTypeMap,
  Category,
  getCategoryIds,
  RootCategory,
  findChapterNoFromKuralNo,
  IndexMapType,
} from 'src/lib/api';
import { AppState } from 'src/state';

export type RouteProp = {
  id: number;
  value: string;
  type: Category | 'top';
  route?: string;
  color?: string;
};
type TopRouteMap = Partial<Record<RootCategory, number>>;
type RouteMap = Partial<Record<Category, number>>;

const CHIP_COLORS = {
  top: 'blueviolet',
  section: 'rgba(162, 43, 47,0.8)',
  literature: 'cadetblue',
  chapter: 'rgba(215, 22, 64, 0.8)',
  kural: '#3f51b5',
};

const getRoutes: (categories: Partial<CategoryBaseTypeMap>) => RouteProp[] = (
  categories
) => {
  // TODO: move static content to firestore
  const routes: RouteProp[] = [
    {
      id: 0,
      value: '',
      type: 'top',
      route: '/list/section',
      color: CHIP_COLORS.top,
    },
  ];
  if (categories.section) {
    routes.push({
      ...categories.section,
      type: 'section',
      route: `/list/${categories.section.id}/literature`,
      color: CHIP_COLORS.section,
    });
  }
  if (categories.literature) {
    routes.push({
      ...categories.literature,
      type: 'literature',
      route: `/list/${categories.literature.id}/chapter`,
      color: CHIP_COLORS.literature,
    });
  }
  if (categories.chapter) {
    routes.push({
      ...categories.chapter,
      type: 'chapter',
      route: `/list/${categories.chapter.id}/kural`,
      color: CHIP_COLORS.chapter,
    });
  }
  if (categories.kural) {
    routes.push({
      ...categories.kural,
      id: categories.kural.id,
      value: `-${categories.kural.id}`,
      type: 'kural',
      route: `/list/kural/${categories.kural.id}`,
      color: CHIP_COLORS.kural,
    });
  }
  return routes;
};

const rootCategoryFinder = (
  INDEX_MAP: IndexMapType,
  options: TopRouteMap
): TopRouteMap => {
  const category = Object.keys(options).find(
    (k: string) => options[k as RootCategory]
  ) as RootCategory;
  return INDEX_MAP && category
    ? // eslint-disable-next-line @typescript-eslint/no-non-null-assertion
      getCategoryIds(INDEX_MAP, category, options[category]!!)
    : {};
};

type GetRouteValueType = () => Partial<CategoryBaseTypeMap>;
type useRouteType = (filter: RouteMap) => [RouteMap, RouteProp[]];
export const useRoute: useRouteType = (filter) => {
  const { section, literature, chapter, index: INDEX_MAP } = useSelector(
    (st: AppState) => st.kuralIndexMap
  );
  const [routeMap, setRouteMap] = React.useState<RouteMap>({});

  React.useEffect(() => {
    if (INDEX_MAP && Object.keys(routeMap).length === 0) {
      if (filter.kural) {
        filter.chapter = findChapterNoFromKuralNo(filter.kural);
      }
      setRouteMap({
        ...rootCategoryFinder(INDEX_MAP, {
          chapter: filter.chapter,
          literature: filter.literature,
          section: filter.section,
        }),
        ...(filter.kural ? { kural: filter.kural } : {}),
      });
    }
  }, [
    INDEX_MAP,
    routeMap,
    filter.kural,
    filter.chapter,
    filter.literature,
    filter.section,
  ]);

  const getRouteValue: GetRouteValueType = React.useCallback(() => {
    return Object.keys(routeMap).reduce((oldVal, k) => {
      const input = k as Category;
      let output;
      switch (input) {
        case 'section':
          output = section[routeMap[input]!!];
          break;
        case 'literature':
          output = literature[routeMap[input]!!];
          break;
        case 'chapter':
          output = chapter[routeMap[input]!!];
          break;
        default:
          // NOTE: setting kural value to empty string as this value not needed
          output = { id: routeMap[input]!!, value: '' };
          break;
      }
      return { ...oldVal, [input]: output };
    }, {});
  }, [chapter, literature, routeMap, section]);

  return [routeMap, getRoutes(getRouteValue())];
};
