import { isCategoryValid, BaseType, Category } from 'src/lib/api';

import { useNavigation } from './useNavigation';

type SubCategoryListType = Exclude<Category, 'chapter' | 'kural'> | 'top';

type useCategoryType = (
  type: SubCategoryListType,
  key: number
) => [BaseType[], (item: BaseType) => void];
export const useCategory: useCategoryType = (type, key) => {
  const [navigateTo] = useNavigation();
  if (type !== 'top') {
    isCategoryValid(type, key);
  }
  // const items =
  //    type === 'top'
  //     ? getAllSections()
  //     : type === 'section'
  //     ? getLiteraturesBySection(key)
  //     : getChaptersByLitrature(key);
  const onItemSelected = (item: BaseType): void => {
    switch (type) {
      case 'top':
        navigateTo(`/list/${item.id}/literature`)();
        break;
      case 'section':
        navigateTo(`/list/${item.id}/chapter`)();
        break;
      case 'literature':
        navigateTo(`/list/${item.id}/kural`)();
        break;
    }
  };
  return [[], onItemSelected];
};
