import React from 'react';

import { Container, makeStyles, Chip } from '@material-ui/core';
import { useDispatch } from 'react-redux';
import { useParams, useLocation, RouteProps } from 'react-router-dom';
import { Category } from 'src/lib/api';
import { RouteProp, useRoute, useNavigation } from 'src/lib/hooks';
import { setTitle, setDefaultTitle } from 'src/state/title';

const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
    flexDirection: 'row',
    justifyContent: 'start',
    alignItems: 'center',
    flexWrap: 'wrap',
    paddingTop: theme.spacing(1),
    paddingBottom: theme.spacing(1),
    margin: 0,
  },
  chipRoot: {
    minWidth: '15vw',
    fontSize: '0.7em',
    height: '1.5em',
    margin: `0 1vw 4px 0`,
    [theme.breakpoints.up('sm')]: {
      maxWidth: 200,
    },
  },
}));

export type KuralChipItem = {
  label: string;
  navRoute: string;
  color: 'default' | 'primary' | 'secondary';
};
type RouteViewerProps = {};

const RouteViewer: React.FC<RouteViewerProps> = (props: RouteViewerProps) => {
  const classes = useStyles(props);
  const dispatch = useDispatch();
  const location = useLocation();
  // eslint-disable-next-line prefer-const
  let { section, chapter, literature, kural } = useParams<
    Partial<Record<Category, string>>
  >();
  console.log(
    'section, chapter, literature, kural',
    section,
    chapter,
    literature,
    kural
  );
  const [navigateTo] = useNavigation();
  const [, routes] = useRoute({
    section: section ? Number(section) : undefined,
    literature: literature ? Number(literature) : undefined,
    chapter: chapter ? Number(chapter) : undefined,
    kural: kural ? Number(kural) : undefined,
  });
  React.useEffect(() => {
    const currentRoute = routes[routes.length - 1];
    dispatch(
      currentRoute.type === 'kural'
        ? setDefaultTitle()
        : setTitle(currentRoute.value)
    );
  }, [dispatch, location, routes]);

  return (
    <Container>
      {routes.map((item, index) => (
        <Chip
          size='small'
          label={item.value}
          color={'primary'}
          onClick={(): void => {
            if (item.route) {
              navigateTo(item.route)();
            }
          }}
          style={{ backgroundColor: item.color }}
          classes={{ root: classes.chipRoot }}
          key={index}
        />
      ))}
    </Container>
  );
};

export default RouteViewer;
