import React from 'react';

import { useCategory } from 'src/lib/hooks';

import RouteViewer from './../../components/kural/RouteViewer';
import ListView from './../common/ListView';

type SectionViewerProps = {
  className?: string;
};

const SectionViewer: React.FC<SectionViewerProps> = (
  props: SectionViewerProps
) => {
  const [items, onSelected] = useCategory('top', 0);
  return (
    <>
      <RouteViewer />
      <ListView
        className={props.className}
        items={items}
        onItemSelected={(item): void => onSelected(item)}
      />
    </>
  );
};

export default SectionViewer;
